import { baseUrl, timeout, enableLog } from "@/config";
import Method from "@/enums/Method";
import { Result, RequestOptions } from "@/model/Result";

// 
const header = { "Content-Type": "application/json; charset=UTF-8" };

// 
const log = {
  info: (message: string, data?: any) => {
    if (enableLog) {
      console.log(`[REQUEST INFO] ${message}`, data || '');
    }
  },
  error: (message: string, error?: any) => {
    if (enableLog) {
      console.error(`[REQUEST ERROR] ${message}`, error || '');
    }
  },
  warn: (message: string, data?: any) => {
    if (enableLog) {
      console.warn(`[REQUEST WARN] ${message}`, data || '');
    }
  }
};

// token
const getToken = (): string => {
  try {
    return uni.getStorageSync('token') || '';
  } catch (error) {
    log.error('token', error);
    return '';
  }
};

// token
const removeToken = (): void => {
  try {
    uni.removeStorageSync('token');
  } catch (error) {
    log.error('token', error);
  }
};

// 
const handleError = (code: number, message: string, data?: any) => {
  switch (code) {
    case 401:
      uni.showModal({
        title: '',
        content: '',
        showCancel: false,
        success: () => {
          removeToken();
          // 
          uni.reLaunch({
            url: '/pages/login/login'
          });
        }
      });
      break;
    case 403:
      uni.showToast({
        title: '',
        icon: 'none',
        duration: 2000
      });
      break;
    case 404:
      uni.showToast({
        title: '',
        icon: 'none',
        duration: 2000
      });
      break;
    case 500:
      uni.showToast({
        title: '',
        icon: 'none',
        duration: 2000
      });
      break;
    default:
      uni.showToast({
        title: message || '',
        icon: 'none',
        duration: 2000
      });
  }
};

const request = <T>(
  url: string,
  options: RequestOptions = { method: Method.GET }
): Promise<T> => {
  return new Promise<T>((resolve, reject) => {
    const { method, data, gateway, loading, headers, remoteUrl } = options;
    const tokenStr = getToken();
    const token = tokenStr ? { Authorization: `Bearer ${tokenStr}` } : {};
    const targetHeader = headers ? headers : header;
    //  baseUrl
    const targetUrl = remoteUrl ? remoteUrl : baseUrl;
    const finalUrl = gateway ? targetUrl + gateway + url : targetUrl + url;
    
    // 
    log.info(`: ${method} ${finalUrl}`, { data, headers: targetHeader });
    
    // 
    loading && uni.showLoading({
      title: '...',
      mask: true
    });
    
    uni.request({
      url: finalUrl,
      method,
      data,
      timeout,
      header: { ...targetHeader, ...token },
      success: ({ data, statusCode }) => {
        log.info(`: ${statusCode}`, data);
        
        // 200
        if (statusCode !== 200) {
          log.error(`HTTP: ${statusCode}`);
          handleError(statusCode, '');
          reject(new Error(`HTTP ${statusCode}`));
          return;
        }
        
        // 
        const result = data as Result<T>;
        const { code, data: resData, message } = result;
        
        // 
        if (code !== undefined && code !== null && code !== 200) {
          log.error(`: ${code}`, { message, data: resData });
          handleError(code, message || '');
          reject(result);
          return;
        }
        
        resolve(resData);
      },
      fail: ({ errMsg }) => {
        log.error('', errMsg);
        uni.showModal({
          title: "",
          content: "",
          showCancel: false,
        });
        reject(new Error(errMsg));
      },
      complete: () => {
        // 
        loading && uni.hideLoading();
      },
    });
  });
};

/**
 * 
 * @param url 
 * @param name 
 * @param path 
 * @param options 
 * @returns Promise<T>
 */
export const uploadFile = <T>(
  url: string,
  name: string,
  path: string,
  options: RequestOptions = { method: Method.POST }
): Promise<T> => {
  const { data: formData, gateway, loading } = options;
  const tokenStr = getToken();
  const finalUrl = gateway ? baseUrl + gateway + url : baseUrl + url;
  
  log.info(`: ${finalUrl}`, { name, path, formData });
  
  // 
  loading && uni.showLoading({
    title: '...',
    mask: true
  });
  
  return new Promise<T>((resolve, reject) => {
    uni.uploadFile({
      url: finalUrl,
      filePath: path,
      name: name,
      formData: formData,
      header: tokenStr ? { Authorization: `Bearer ${tokenStr}` } : {},
      success: ({ data, statusCode }) => {
        log.info(`: ${statusCode}`, data);
        
        try {
          // 200
          if (statusCode !== 200) {
            log.error(`HTTP: ${statusCode}`);
            handleError(statusCode, '');
            reject(new Error(`HTTP ${statusCode}`));
            return;
          }
          
          const parsedData = JSON.parse(data);
          
          // 
          if (parsedData.hasOwnProperty('code') || parsedData.hasOwnProperty('data') || parsedData.hasOwnProperty('message')) {
            // 
            const result = parsedData as Result<T>;
            const { code, data: resData, message } = result;
            
            // 
            if (code !== undefined && code !== null && code !== 200) {
              log.error(`: ${code}`, { message, data: resData });
              handleError(code, message || '');
              reject(result);
              return;
            }
            
            resolve(resData);
          } else {
            //  HivisionIDPhotos API
            resolve(parsedData as T);
          }
        } catch (error) {
          log.error('', error);
          uni.showToast({
            title: '',
            icon: 'none',
            duration: 2000
          });
          reject(error);
        }
      },
      fail: (err) => {
        log.error('', err);
        uni.showModal({
          title: "",
          content: "",
          showCancel: false,
        });
        reject(err);
      },
      complete: () => {
        // 
        loading && uni.hideLoading();
      }
    });
  });
};

// 
export { getToken, removeToken, log };

export default request;
